import React, { useState } from "react";
import NavDashBoard from ".././NavDashBoard";
import axios from "../../utils/API"
import {  useHistory } from "react-router-dom";

const AddUsers = () => {


  const idusers = Math.floor(Math.random() * 178664214);
  const [iduser, setiduser] = useState(idusers);
  const [username, setusername] = useState("");
  const [passwordusers, setpasswordusers] = useState("");
  const [phone, setphone] = useState("");
  const [address, setaddress] = useState("");
  const [stateuser, setastateuser] = useState("");
  const [roleuser, setroleuser] = useState("");

const userid= localStorage.getItem("idusee")
  const history = useHistory();
  const addnewuser = async (event) => {
 event.persist()
    const userone = await axios.post(`users/${userid}`);
    if (userone.data.permistoin_users[0].adduser === "") {
      alert("    ")
    }
    else {
      if (username==="") {
              alert("   ")

      }
     else if (passwordusers === "") {
        alert("    ")

      }
      else {
        
      
      
       
        //
        setusername("");
        setpasswordusers("");
        setphone("");
        setaddress("");
        const idusers = Math.floor(Math.random() * 178664214);
        setiduser(idusers)
         axios.post("users", { iduser: iduser, username: username, password: passwordusers, phone: phone, address: address, state: stateuser, roleuser: roleuser })
          alert("   ")
      }
    }
  }
  const [loadingpage, setloadingpage] = useState(true)
  const puser = async () => {
    const userone =await axios.post(`users/${userid}`)
    if (userone.data.role === "") {
      history.push("/Mustakdem", { from: "ProtectedRoute" });
      setloadingpage(true)
           
    }

    else {
       setloadingpage(false)
           
    }
  }

  puser()
  
  return (
    <div>
      {loadingpage &&
        <div>
        <div className="overflowimg"></div>
        <img src={window.location.origin + '/img/loding.gif'} className="img_loding" alt="logo" /></div>}

      
      <NavDashBoard />
      <div className="users_header">  </div>
      <div className="container addusernew">
        <div className="row">
          <div className="col-sm-12 col-md-6 mt-3">
            <div className="input-group">
              <div className="input-group-prepend">
                <span className="input-group-text"> </span>
              </div>
              <input
                type="text"
                aria-label="First name"
                className="form-control"
                value={username}
                onChange={(e)=>(setusername(e.target.value))}
              />
            </div>
          </div>
          <div className="col-sm-12 col-md-6 mt-3">
            <div className="input-group">
              <div className="input-group-prepend">
                <span className="input-group-text"> </span>
              </div>
              <input
                type="text"
                aria-label="First name"
                className="form-control"
                 value={iduser}
                onChange={(e)=>(setiduser(e.target.value))}
              />
            </div>
          </div>

          <div className="col-sm-12 col-md-6 mt-3">
            <div className="input-group">
              <div className="input-group-prepend">
                <span className="input-group-text"> </span>
              </div>
              <input
                type="text"
                aria-label="First name"
                className="form-control"
                 value={passwordusers}
                onChange={(e)=>(setpasswordusers(e.target.value))}
              />
            </div>
          </div>

          <div className="col-sm-12 col-md-6 mt-3">
            <div className="input-group">
              <div className="input-group-prepend">
                <span className="input-group-text"> </span>
              </div>
              <input
                type="text"
                aria-label="First name"
                className="form-control"
                 value={phone}
                onChange={(e)=>(setphone(e.target.value))}
              />
            </div>
          </div>

          <div className="col-sm-12 col-md-6 mt-3">
            <div className="input-group">
              <div className="input-group-prepend">
                <span className="input-group-text"></span>
              </div>
              <input
                type="text"
                aria-label="First name"
                className="form-control"
                 value={address}
                onChange={(e)=>(setaddress(e.target.value))}
              />
            </div>
          </div>
          
<div className="col-sm-12 col-md-3 mt-3">
            <div className="input-group">
              <div className="input-group-prepend">
                <span className="input-group-text"> </span>
              </div>
              <select className="form-control p-0"  value={roleuser}
                onChange={(e)=>(setroleuser(e.target.value))}>
                <option></option>
                <option> </option>
              </select>
            </div>
          </div>

          <div className="col-sm-12 col-md-3 mt-3">
            <div className="input-group">
              <div className="input-group-prepend">
                <span className="input-group-text"></span>
              </div>
              <select className="form-control p-0" value={stateuser}
                onChange={(e)=>setastateuser(e.target.value)}>
                <option></option>
                <option></option>
              </select>
            </div>
          </div>
          <div className="col-sm-12 col-md-12 mt-3">
           
            <button onClick={addnewuser} className="btn btn-primary" >
               
              
            </button>
            
           
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddUsers;
