import React, { useState } from 'react'
import "./style.css"
import * as Fa from 'react-icons/fa';
import * as Ai from 'react-icons/ai';
import { Link } from 'react-scroll';
import * as LinkA from "react-router-dom"
import * as Gr from 'react-icons/gr';
import * as Ri from 'react-icons/ri';
import {IoMdArrowDropup} from 'react-icons/io';

import ScrollToTop from "react-scroll-up";

const NavBar = () => {

    const [navmobail, setnavmobail] = useState(false);


    
    return (
        <div>
            <div className={navmobail ? " active_navmobil" : "navbar_mobail"}>
                 <div className="content_navmobail">
            <img src={window.location.origin + '/img/logo.png'} className="logo_img log" alt="logo" />
             <div className="sadeytxt" alt="logo">
                                   
                                <p style={{fontFamily:"'Aref Ruqaa', serif",fontSize:30,color:"rgb(255 195 26)",marginBottom:0}}> </p>
            </div>
            <h4 className="txt_content_navmobail">  <Link onClick={()=>setnavmobail(false)} style={{color:'black',display:"flex",justifyContent:"space-between"}}    activeClass="active" to="home" spy={true} smooth={true} duration={1500}><Ai.AiFillHome  style={{marginRight:5,color:'black'}}/></Link></h4>
                <h4 className="txt_content_navmobail">  <Link onClick={()=>setnavmobail(false)}  style={{color:'black',display:"flex",justifyContent:"space-between"}}    activeClass="active" to="kadamat" spy={true} smooth={true} duration={1500}> <Gr.GrServices  style={{marginRight:5,color:'black'}}/></Link></h4>
            <h4 className="txt_content_navmobail">
                  <Link onClick={()=>setnavmobail(false)} style={{color:'black',display:"flex",justifyContent:"space-between"}}    activeClass="active" to="who_are" spy={true} smooth={true} duration={1500}> <Ri.RiContactsBook2Fill  style={{marginRight:5,color:'black'}}/></Link>
                </h4>
            <h4 className="txt_content_navmobail">
                 <Link onClick={()=>setnavmobail(false)} style={{color:'black',display:"flex",justifyContent:"space-between"}}    activeClass="active" to="contact" spy={true} smooth={true} duration={1500}> <Ai.AiFillContacts  style={{marginRight:5,color:'black'}}/></Link>
                </h4>
            <h4 className="txt_content_navmobail txt_login" >
                 <LinkA.Link style={{color:'black',display:"flex",justifyContent:"space-between"}}     to="/login">   <Fa.FaUserCircle  style={{marginRight:5,color:'black'}}/></LinkA.Link>
               </h4>
             
            </div>
                <div className="close_navmobail" onClick={()=>setnavmobail(!navmobail)}>X</div>
             </div>

            <div className="nav_mobail">
                <Ai.AiOutlineMenu size={30} style={{ color: "#fff", cursor: "pointer" }} onClick={()=>setnavmobail(true)}/>
               
                <img src={window.location.origin + '/img/logo.png'} className="logo_img" alt="logo" />

                 <div className="sadeytxt" alt="logo">
                                   
                                <p style={{fontFamily:"'Aref Ruqaa', serif",fontSize:30,color:"rgb(255 195 26)",marginBottom:0}}> </p>
                  </div>
               
            </div>
           
        <nav >
           
            <div className="nav">
                <div className="container">
                        {/*top header */}

                     <div className="nav_top">
                    <div className="content_elemnt_right">
                      <a href="/">  :      8   10 </a>
                    </div>
                    <div className="content_elemnt_left">
                        <a href="/"><Fa.FaFacebookF /></a>
                        <a href="/"><Fa.FaWhatsapp /></a>
                        <a href="/"><Fa.FaViber /></a>
                    </div>
                    </div>

                  
                </div>
                
            </div>
    {/*header main*/}
            <div className="nav_header">
                <div className="container">
                    <div className="header_main">
                       
                        <div style={{textAlign:"center",fontSize:15}}>
                                   
                                <p style={{fontFamily:"'Aref Ruqaa', serif",fontSize:30,color:"rgb(255 195 26)",marginBottom:0}}> </p>
                        </div>
                        
                        <div className="header_main_right">
                            <div className="header_main_phone">
                            <h5 > : 964+<br/></h5>
                            <h5>07809944088<br/>07601666366</h5>
                            </div>
                            <div className="header_main_address">
                            <h5>:<br/></h5>
                            <h5>- <br/>    (  )   </h5>
                            </div>
                          
                            
                        </div>
                        <div className="header_main_left">
                            <div className="header_main_logo">
                              
                                
                                <img src={window.location.origin +'/img/logo.png'} className="logo_img" alt="logo"/> 
                               
                            </div>
                        </div>

                    </div>
                </div>
            </div>
            
            {/*nav header */}
            <div className="header_navbar">
                <div className="container">
                    <div className="header_navbar_main">
                        <div className="header_navbar_right">
                        <Link className="txt_content_navmobail" activeClass="active" to="home" spy={true} smooth={true} duration={1500}></Link>
                        <Link className="txt_content_navmobail" activeClass="active" to="who_are" spy={true} smooth={true} duration={1500}> </Link>
                        <Link className="txt_content_navmobail" activeClass="active" to="kadamat" spy={true} smooth={true} duration={1500}></Link>
                               
                                     <Link className="txt_content_navmobail" activeClass="active" to="contact" spy={true} smooth={true} duration={1500}> </Link>
                   
                               
                        </div>
                        <div className="header_navbar_left">
                        <LinkA.Link  to="/login">   <Fa.FaUserCircle style={{marginRight:5}}/></LinkA.Link>
                           
                        </div>
                      
                    </div>
                   
                </div>
            </div>

            </nav>
    
            
            <ScrollToTop duration={1500} style={{ background: "rgb(255 0 0)", width: 50, height: 50, borderRadius: '50%', color: "#fff", display: "flex", justifyContent: "center", alignItems: "center", fontWeight: 700 ,flexDirection:"column",zIndex:5125555,border:"1px solid black"}} showUnder={160}>
                <IoMdArrowDropup />
                <span> UP</span>
</ScrollToTop>
            
            </div>
    )
}

export default NavBar
